// Mixins.

.nl() {
    margin: 0;
    padding: 0;
    text-indent: 0;
    list-style: none;
    list-style-position: outside;
}

.cf() {
    &:before,
    &:after {
        display: table;
        content: "";
    }

    &:after {
        clear: both;
    }

    .lt-ie8 & {
        zoom: 1;
    }
}

.blocks-justify() {
    text-align: justify;
    text-justify: newspaper;
}

.reset() {
  margin: 0;
  padding: 0;
}

// You can add your own mixins here: